@use "global" as *;
.delayup__card {
  opacity: 0;
}

.delayup {
  animation-name: delayUpAnime;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  opacity: 0;
}
  
  @keyframes delayUpAnime{
    from {
      opacity: 0;
      transform: translateY(90px);
    }
  
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }